<template>
    <div v-if="deviceType===1" class="layout">
        <Layout>
            <Header>
                <Menu mode="horizontal" theme="dark" @on-select="toRouter" :active-name="navList[0].path" accordion>
                    <div class="layout-nav">
                        <div v-for="(item,index) in navList" :key="index">
                            <MenuItem v-if="!item.children" :name="item.path">
                                <Icon type="navigate"></Icon>
                                {{item.title}}
                            </MenuItem>
                            <Submenu v-else :name="item.path">
                                <template slot="title">
                                    <Icon type="navigate"></Icon>
                                    {{item.title}}
                                </template>
                                <MenuItem v-for="(innerItem,innerIndex) in item.children" :key="innerIndex" :name="innerItem.path">{{innerItem.title}}</MenuItem>
                            </Submenu>
                        </div>
                    </div>
                </Menu>
            </Header>
            <Content>
                <Card class="center-content">
                    <slot name="content"></slot>
                </Card>
            </Content>
            <Footer class="layout-footer-center">2018-2019 &copy; 电影推荐网版权所有</Footer>
        </Layout>
    </div>
    <div class="container" v-else-if="deviceType===0">
        <mt-header class="nav-header" title="青海旅游网">
            <router-link to="/" slot="left">
                <mt-button icon="back">back</mt-button>
            </router-link>
            <mt-button icon="more" slot="right"></mt-button>
        </mt-header>
        <mt-navbar class="children-nav" v-if="navList[selectedMobileIndex].children&&navList[selectedMobileIndex].children.length>0" v-model="selectedChildrenIndex">
            <mt-tab-item v-for="(item,index) in navList[selectedMobileIndex].children" :key="index" :id="index">{{item.title}}</mt-tab-item>
        </mt-navbar>
        <Content :style="{padding: '0 50px'}">
            <Card class="center-content">
                <slot name="content"></slot>
            </Card>
        </Content>
        <mt-tabbar class="nav-tab" v-model="selectedMobileIndex">
            <mt-tab-item v-for="(item,index) in navList" :key="index" :id="index">
                <img slot="icon" :src="item.icon">
                {{item.title}}
            </mt-tab-item>
        </mt-tabbar>
    </div>
    <div v-else-if="deviceType===2" class="layout">
        <Layout>
            <Header class="header-title">
                {{managementTitle}}
                <Button @click="logout" type="ghost" class="log-out"><Icon type="log-out"></Icon>退出登录</Button>
            </Header>
            <Row>
                <Col span="4">
                    <Menu :theme="theme2">
                        <Submenu v-for="(item,index) in managementMenu" :key="index" :name="index">
                            <template slot="title">
                                <Icon type="ios-paper"></Icon>
                                {{item.title}}
                            </template>
                            <MenuItem v-for="(innerItem,innerIndex) in item.children" :key="innerIndex" :name="index-innerIndex">{{innerItem.title}}</MenuItem>
                        </Submenu>
                    </Menu>
                </Col>
                <Col span="20">
                    <Content class="right-container" :style="{padding: '0 50px'}">
                        <Card class="center-content">
                            <slot name="content"></slot>
                        </Card>
                    </Content>
                </Col>
            </Row>
            <Footer class="layout-footer-center">2018-2019 &copy; 电影推荐网版权所有</Footer>
        </Layout>
    </div>
</template>

<script>
    import commonJS from '../common/common';
    export default {
        name: '',
        data() {
            return {
                title:'电影',
                bannerImg:require('../../src/assets/frontend/banner.png'),
                navList:[{
                    title:'首页',
                    path:'',
                    icon:require('../assets/frontend/common/index.png'),
                },{
                    title:'精彩电影',
                    path:'scenicSpot',
                    icon:require('../assets/frontend/common/index.png'),
                },{
	                title:'电影推荐',
	                path:'recommend',
	                icon:require('../assets/frontend/common/index.png'),
                },{
                    title:'电影查询',
                    path:'route',
                    icon:require('../assets/frontend/common/route.png'),
                },{
                    title:'用户',
                    path:'food',
                    icon:require('../assets/frontend/common/food.png'),
                },],
                selectedIndex:0,
                selectedMobileIndex:0,
                selectedChildrenIndex:0,
                deviceType:1,//0为移动端，1为PC前台展示,2为PC后台管理
            }
        },
        watch:{
            selectedMobileIndex(){
                this.$router.push("/"+this.navList[this.selectedMobileIndex].path);
            },
            selectedChildrenIndex(){
                this.$router.push("/"+this.navList[this.selectedMobileIndex].children[this.selectedChildrenIndex].path);
            },
        },
        methods: {
            toRouter(path){
                this.$router.push("/"+path);
            },
            showChildren(item,index){
                item.isShowChildren=true;
                this.navList.splice(index,1,item);
            },
            hideChildren(item,index){
                item.isShowChildren=false;
                this.navList.splice(index,1,item);
            },
        },
        created() {

        },
    }
</script>

<style>

</style>



